<template>
  <div>
    <div class="list">
      <p class="title">我的操作</p>
      <ul>
        <router-link tag="li" to="/user/project-index">
          <p>
            <img src="../../assets/img/notice-3.png" alt />
          </p>
          <p class="p1">{{data.statistics.projectCount}}</p>
          <p class="p2">我的项目</p>
        </router-link>

        <router-link tag="li" to="/user/capital-index">
          <p>
            <img src="../../assets/img/notice-4.png" alt />
          </p>
          <p class="p1">{{data.statistics.moneyCount}}</p>
          <p class="p2">我的资金</p>
        </router-link>

        <router-link tag="li" to="/user/capital-collection-index">
          <p>
            <img src="../../assets/img/notice-1.png" alt />
          </p>
          <p class="p1">{{data.statistics.projectCollect}}</p>
          <p class="p2">项目收藏</p>
        </router-link>
        <router-link tag="li" to="/user/collection-index">
          <p>
            <img src="../../assets/img/notice-5.png" alt />
          </p>
          <p class="p1">{{data.statistics.moneyCollect}}</p>
          <p class="p2">资金收藏</p>
        </router-link>
        <router-link tag="li" to="/user/capital-project-index">
          <p>
            <img src="../../assets/img/notice-2.png" alt />
          </p>
          <p class="p1">{{data.statistics.projectUnite}}</p>
          <p class="p2">我的合作</p>
        </router-link>
      </ul>
      <div class="cl"></div>
    </div>
    <div class="news">
      <p class="title">通知中心</p>
      <Tabs :value="active" style="padding:20px;" @on-click="tabIndex">
        <TabPane
          id="tab"
          v-for="(item, index) in tabsMenus"
          :key="index"
          :label="item.label"
          :name="item.index"
        >
          <ul>
            <li v-for="(item, index) in data.messageReminds.rows" :key="index">
              <p class="info_title">
                {{item.title}}
                <span>{{item.createTime}}</span>
              </p>
              <div class="info_content">
                <!-- 系统 -->
                <div v-if="item.type == 0">
                  <router-link
                    style="color:#2868d1;"
                    :to="item.systemType == 1 ? `/project/detail/${item.dataId}` : item.systemType == 2 ? `/capital/detail/${item.dataId}` : item.systemType == 3 ? `/user/certify-index` : '' "
                  >
                    <span
                      style="color:#eb655c;"
                    >{{userInfo.remind.systemRemind.indexOf(item.id) != '-1' ? '[新消息]' : ''}}</span>
                    {{item.dataName}}
                  </router-link>
                  {{item.content}}
                  <span class="close" @click="noticeDelRemind(item.id)">@删除通知</span>
                </div>
                <!-- 投递 -->
                <div v-else-if="item.type == 1">
                  <router-link style="color:#2868d1;" :to="`/project/detail/${item.dataId}`">
                    <span
                      style="color:#eb655c;"
                    >{{userInfo.remind.deliveryRemind.indexOf(item.id) != '-1' ? '[新消息]' : ''}}</span>
                    {{item.dataName}}
                  </router-link>
                  {{item.content}}
                  <router-link :to="`/user/capital-delivery-index`">, 点击前往收到投递列表</router-link>
                  <span class="close" @click="noticeDelRemind(item.id)">@删除通知</span>
                </div>
                <!-- 协议 -->
                <div v-else-if="item.type == 2">
                  <router-link style="color:#2868d1;" :to="`/project/detail/${item.dataId}`">
                    <span
                      style="color:#eb655c;"
                    >{{userInfo.remind.termSheetRemind.indexOf(item.id) != '-1' ? '[新消息]' : ''}}</span>
                    {{item.dataName}}
                  </router-link>
                  {{item.content}}
                  <router-link :to="`/user/capital-treaty`">, 点击前往协议列表</router-link>
                  <span class="close" @click="noticeDelRemind(item.id)">@删除通知</span>
                </div>
              </div>
            </li>
          </ul>
          <div class="page">
            <br />
            <br />
            <Page :total="data.messageReminds.total" @on-change="pageChange" />
          </div>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>
<script>
import { Tabs, TabPane, Button, Poptip, Page } from "iview";
import { getRemind, noticeDelRemind } from "../../api/user";
import { messages } from "../../utils";
import { mapActions, mapState } from "vuex";
export default {
  components: {
    [Tabs.name]: Tabs,
    [Button.name]: Button,
    [Poptip.name]: Poptip,
    [Page.name]: Page,
    [TabPane.name]: TabPane
  },
  computed: {
    ...mapState("user", ["userInfo"])
  },
  data() {
    return {
      active: "0",
      data: {
        statistics: {},
        messageReminds: {
          deliveryRemindCount: 0,
          systemRemindCount: 0,
          termSheetRemindCount: 0,
          rows: []
        }
      },
      // tab导航栏
      tabsMenus: [
        {
          label: h => {
            return h("div", [
              h("span", "系统消息"),
              this.data.messageReminds.systemRemindCount != 0
                ? h("a", this.data.messageReminds.systemRemindCount)
                : ""
            ]);
          },
          index: "0"
        },
        {
          label: h => {
            return h("div", [
              h("span", "投递消息"),
              this.data.messageReminds.deliveryRemindCount != 0
                ? h("a", this.data.messageReminds.deliveryRemindCount)
                : ""
            ]);
          },
          index: "1"
        },
        {
          label: h => {
            return h("div", [
              h("span", "协议消息"),
              this.data.messageReminds.termSheetRemindCount != 0
                ? h("a", this.data.messageReminds.termSheetRemindCount)
                : ""
            ]);
          },
          index: "2"
        }
      ],
      //tabs选项栏配置
      seachConfig: [
        {
          type: 0, // 系统消息
          pageSize: 10,
          pageNo: 1
        },
        {
          type: 1, // 投递消息
          pageSize: 10,
          pageNo: 1
        },
        {
          type: 2, // 协议消息
          pageSize: 10,
          pageNo: 1
        }
      ],
      userInfoData: {}
    };
  },
  methods: {
    ...mapActions("user", ["getUserInfo"]),
    /**
     * @function 通知栏点击事件
     * @param {当前通知栏点击的下标} index
     */
    tabIndex(index) {
      this.active = index;
      this.getData();
    },
    //数据获取
    getData() {
      getRemind({
        type: this.seachConfig[this.active].type,
        pageSize: this.seachConfig[this.active].pageSize,
        pageNo: this.seachConfig[this.active].pageNo
      }).then(res => {
        this.data = res.data.data;
      });
    },
    /**
     * @function 删除通知
     * @param {当前通知的ID值} id
     */
    noticeDelRemind(id) {
      noticeDelRemind({ id }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success("删除成功");
          this.getData();
        }
      });
    },
    //分页点击
    pageChange(page) {
      this.data.messageReminds.rows = [];
      this.seachConfig[this.active].pageNo = page;
      this.getData();
    }
  },
  created() {
    this.getUserInfoData = this.userInfo;
    this.getData();
  },
  beforeDestroy() {
    this.getUserInfo();
  }
};
</script>

